
div.card.scorecard-card {
  background-color: rgba(98, 188, 250, 1);
  position: fixed;
}
.record {
  color: #6CADED;
}
.pers-record {
  color: #ED6C6C;
}
label.scorecard-label {
  color: white;
  font-size: 10px;
}
div.card.scorecard_container.expand {
  top: 0;
  left: 0;
  padding: 0;
  z-index: 8888;
  width: 100%;
  height: 100%;
}
div.card__title.scorecard-card.pa-0.fix_header {
  position: fixed;
  z-index: 100;
  width: 100%;
  -webkit-box-shadow: 0 10px 13px -6px rgba(0,0,0,0.2), 0 20px 31px 3px rgba(0,0,0,0.14), 0 8px 38px 7px rgba(0,0,0,0.12) !important;
          box-shadow: 0 10px 13px -6px rgba(0,0,0,0.2), 0 20px 31px 3px rgba(0,0,0,0.14), 0 8px 38px 7px rgba(0,0,0,0.12) !important;
}
.slide-fade-enter-active {
  -webkit-transition: all .8s ease;
  transition: all .8s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all .0s;
  transition: all .0s;
  opacity: 0;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  -webkit-transform: translate(200px);
          transform: translate(200px);
  opacity: 0;
}


