
div.layout.record.row.wrap {
  background-color: #6CADED;
}
div.card__actions.text-xs-center.record {
  background-color: #6CADED;
}
div.user-score-card.card.card--flat {
  /*background-color: rgba(153, 153, 153, 1)*/
  /*: 100%;*/
}
div.card__text {
  max-height: 500vh;
}
div.text-xs-left.user-score.card.card--flat {
  height: 500px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.vue-scroll-picker-item.-selected {
  color: #007BFF;
}
div.vue-scroll-picker-item {
    text-align: center;
  height: 1.7em;
  line-height: 1.7em;
}
.vue-scroll-picker-layer {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
   /* .top,
    .middle,
    .bottom {
        position: absolute;
    }*/
.vue-scroll-picker-layer div.top {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid #c8c7cc;
    background: -webkit-gradient(linear,left top, left bottom,color-stop(10%, #fff),to(rgba(255, 255, 255, .1)));
    background: linear-gradient(180deg,#fff 10%,rgba(255, 255, 255, .1));
    top: 0;
    left: 0;
    right: 0;
    height: 40%;
    cursor: pointer;
}
.vue-scroll-picker-layer div.middle {
    top: 40%;
    left: 0;
    right: 0;
    bottom: 40%;
}
.vue-scroll-picker-layer div.bottom {
    border-top: 1px solid #c8c7cc;
    background: -webkit-gradient(linear,left bottom, left top,color-stop(10%, #fff),to(rgba(255, 255, 255, .1)));
    background: linear-gradient(360deg,#fff 10%,rgba(255, 255, 255, .1));
    bottom: 0;
    left: 0;
    right: 0;
    height: 40%;
    cursor: pointer;
}
div.vue-scroll-picker {
  position: relative;
  /*height: auto;*/
  background-color: white;
}
.item {
  border-bottom: 1px solid #eee;
  overflow: hidden;
}
.item:last-child {
  border-bottom: 0;
}
.scroll-container {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}



